import {
  WebComponentOptions,
  defineWebComponent
} from '@websublime/wbs-ui-common';

import { WsInspector } from './inspector';

interface WsInspectorOptions extends WebComponentOptions {
  className?: string[];
  enabled?: boolean;
}

/**
 * @public
 */
const registerComponent = (
  options: WsInspectorOptions = {},
  name = 'ws-inspector'
) =>
  defineWebComponent(name, WsInspector, {
    className: ['ws-inspector'],
    enabled: false,
    ...options
  });

declare global {
  interface HTMLElementTagNameMap {
    'ws-inspector': WsInspector;
  }
}

export { registerComponent };

export { WsInspector } from './inspector';
