import {
  ComponentElement,
  ComponentMetadata,
  Ref,
  property,
  uniqueID
} from '@websublime/wbs-ui-common';
import { LitElement, html } from 'lit';

import { version } from './version';

const metadata: ComponentMetadata = {
  description: 'Inspector container component',
  link: '',
  name: 'ws-inspector',
  qa: 'ws-ui-inspector',
  scope: '@websublime/wbs-ui-inspector',
  version
};

/**
 * Inspector component element
 */
export class WsInspector extends ComponentElement {
  static shadowRootOptions = {
    ...LitElement.shadowRootOptions,
    delegatesFocus: true
  };

  @property({ type: Object })
  el!: { element: Ref<HTMLElement>; registry: ComponentMetadata };

  /**
   * Element id
   */
  private identifier = uniqueID();

  constructor() {
    super(metadata);

    const classes = this.options.className as Array<string>;

    this.classList.add(...classes);
    this.dataset.qa = metadata.qa;
  }

  connectedCallback() {
    super.connectedCallback();

    const { element } = this.el;
    const htmlElement = element.value;

    if (htmlElement && this.inspect) {
      const tag = this.createSurroundingElement();

      htmlElement.style.outline = '2px solid  #ff00ff';
      htmlElement.prepend(tag);

      htmlElement.addEventListener('mouseover', () => {
        htmlElement.style.outline = '2px solid  #ff00ff';
        htmlElement.style.position = 'relative';
        htmlElement.style.borderRadius = '10px';
        htmlElement.prepend(tag);
      });

      htmlElement.addEventListener('mouseout', () => {
        htmlElement.style.outline = '';
        htmlElement.style.borderRadius = '';
        htmlElement.style.position = '';

        tag.remove();
      });
    }
  }

  private createSurroundingElement(): HTMLSpanElement {
    const { registry } = this.el;
    const { scope = '', version = '' } = registry;

    const tag = document.createElement('span');
    tag.textContent = `${scope}@${version}`;
    tag.style.display = 'inline';
    tag.style.borderRadius = '4px';
    tag.style.backgroundColor = '#ff00ff';
    tag.style.color = 'white';
    tag.style.position = 'absolute';
    tag.style.bottom = '-10px';
    tag.style.left = '45%';
    tag.style.padding = '2px 4px';

    return tag;
  }

  render() {
    return html` <template id=${this.identifier}></template> `;
  }
}
